import {
	getIsCardOverIimit,
	getUserCardInfo,
	deleteCardInfo
} from '@/common/api/userInfo.js'
import {
	error
} from '@/common/js/errorTips.js'

export default {
	data() {
		return {
			// x轴方向的偏移
			x: 0,
			// 当前x的值
			currentX: 0,
			show: false,
			iconPath: '/static/center/',
			cardList: [],
			handle: 0, // 0代表没有操作，1代表点击编辑，2代表点击删除
			isGetData: true, // 当用户没有添加过就诊卡时使用
			message: '家人就诊卡', // 用于没有家人就诊卡信息时显示
			isOverLimit: false, // 判断是否超过限制
		}
	},
	methods: {
		test(e) {
			this.currentX = e.detail.x;
		},
		//自动关闭左滑效果
		stopSlideView: function() {
			this.x = 0;
			this.show = false
			this.handle = 0
		},
		touchend(e) {
			if (this.currentX < -46) {
				this.x = -92;
				this.show = e.currentTarget.dataset.name
			} else {
				this.stopSlideView()
			}
		},
		// 点击了编辑
		handleEdit: function(item) {
			// index是这个的编号
			this.handle = 1
			uni.navigateTo({
				url: '/pagesB/pages/center/addCard/editCard/editCard?cardInfo=' +
					JSON.stringify(item)

			})
			uni.showLoading({
				title: '加载中'
			})
			var that = this
			setTimeout(function() {
				that.stopSlideView()
			}, 2000)
		},
		// 点击了删除,其实为解除该就诊卡的绑定
		handleDelete: function(relationId) {
			this.handle = 2 // 表示点击的是删除
			var that = this
			uni.showLoading({
				title: '加载中'
			})
			deleteCardInfo(relationId).then(res => {
				if (res.data.code === 200) {
					this.getUserCardInfo()
					that.stopSlideView()
					uni.hideLoading()
				}
			}).catch(() => {
				uni.hideLoading()
				error('删除失败')
			})
		},
		// 跳转到添加就诊卡页面
		toAddCard: function() {
			uni.navigateTo({
				url: '/pagesB/pages/center/addCard/addCard'
			})
		},
		// 获取用户为家人添加的就诊卡数目是否超过限制
		getIsCardOverIimit: function() {
			getIsCardOverIimit(uni.getStorageSync('accountID')).then(res => {
				if (res.data.code === 200) {
					// 等于false就为还没有超过限制
					if (res.data.data === false) {
						this.isOverLimit = false
					} else {
						this.isOverLimit = true
					}
				}
			}).catch(() => {
				error('网络')
			})
		},
		// 获取用户家人的就诊卡信息
		getUserCardInfo: function() {
			uni.showLoading({
				title: '加载中'
			})
			this.cardList = []
			getUserCardInfo(uni.getStorageSync('accountID')).then(res => {
				if (res.data.code === 200) {
					const data = res.data.data
					let _this = this
					if (data.length > 0) {
						data.forEach(function(item, index) {
							// 表示只为自己添加了就诊卡没有为家人添加
							if (data.length === 1 && item.type === 0) {
								_this.isGetData = false
								return
							}
							if (item.type !== 0) {
								_this.isGetData = true
								_this.cardList.push(({
									relationId: item.relationId,
									type: item.type, // 表示与该账号的关系的类型
									id: item.id, // 就诊卡id
									name: item.name, //就诊卡的姓名
									gender: item.gender, // 就诊卡的性别
									phone: item.phone, // 电话号码
									identify: item.identificationNumber, // 身份证号
									birthDate: item.birthDate // 生日
								}))
							}
						})
					}
					uni.hideLoading()
				}
			}).catch(() => {
				uni.hideLoading()
				error('网络')
			})
		},
		// 点击删除按钮出现的弹框
		openConfirm: function(name, relationId) {
			let that = this
			uni.showModal({
				title: '删除',
				content: '确认解绑' + name + '的就诊卡吗？',
				confirmText: "确认",
				cancelText: "取消",
				success: function(res) {
					console.log(res);
					if (res.confirm) {
						that.handleDelete(relationId)
					} else {
						that.stopSlideView()
					}
				}
			});
		},
	},
	onShow() {
		this.getUserCardInfo()
	}
}
